'use client';

import BookingForm from '@/src/components/bookings/BookingForm';
import BookingTable from '@/src/components/bookings/BookingTable';
import { useBookingData } from '@/src/components/bookings/useBookingData';
import { Card, CardContent } from '@/src/components/ui/card';

export default function BookingManagementPage() {
  const { bookings, editingBooking, setEditingBooking, saveBooking, deleteBooking, loading } = useBookingData();

  return (
    <div className="p-4 space-y-6">
      <Card>
        <CardContent className="p-6">
          <h2 className="text-xl font-semibold mb-4">Booking Form</h2>
          <BookingForm
            editingBooking={editingBooking}
            onSave={saveBooking}
            onCancel={() => setEditingBooking(null)}
          />
        </CardContent>
      </Card>

      <Card>
        <CardContent className="p-6">
          <h2 className="text-xl font-semibold mb-4">Bookings List</h2>
          {loading ? <p>Loading bookings...</p> : (
            <BookingTable
              bookings={bookings}
              onEdit={(b) => setEditingBooking(b)}
              onDelete={(id) => deleteBooking(id)}
            />
          )}
        </CardContent>
      </Card>
    </div>
  );
}
